var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"company"}},[_c('h2',[_vm._v("公司概括")]),_c('p',{staticClass:"style31"},[_vm._v(" 山西杏花村汾酒厂股份有限公司于1993年12月组建成立，1994年1月在上海证券交易所挂牌上市，是中国白酒第一股，山西第一股。公司占地面积5400余亩，总资产80余亿，职工7024人，是以生产经营中国名酒——汾酒、竹叶青酒为主营业务，全国最大的名优白酒生产基地之一。")]),_c('p',[_vm._v("汾酒文化源远流长，是晋商文化的重要一支，与黄河文化一脉相承。汾酒历史上有过四次成名。早在1500年前的南北朝时期，汾酒就作为宫廷御酒受到北齐武成帝的推崇而一举成名，并载入廿四史；晚唐大诗人杜牧的千古绝唱 “借问酒家何处有牧童遥指杏花村”’使汾酒再度成名；1915年，汾酒在巴拿马万国博览会上一举荣获甲等金质大奖章，成为酒品至尊；解放后汾酒五次蝉联国家名酒称号，竹叶青酒三次荣获国家名酒称号。")]),_c('p',[_vm._v("近年来，公司坚持“清香品质为本，历史文化为魂”的品牌定位，不断加强品牌建设工作，明确了以“杏花村”、“竹叶青”、“汾”三个国家驰名商标为核心，依托辉煌厚重历史文化，紧紧围绕“国酒之源、清香之祖、文化之根”’的战略定位，将汾酒打造为中国历史文化名酒第一品牌。汾酒工业园林被授予“全国工业旅游示范点”，杏花村汾酒酿造作坊遗址被列入“全国重点文物保护单位”,汾酒博物馆被评定为首家“国家级酒文化学术活动示范基地”，汾酒酿造工艺成为国家级非物质文化遗产。")]),_c('p',[_vm._v("公司秉承“用心酿造、诚信天下”的企业核心理念，创新提升内部管理，始终将创优品质放在第一位。公司相继荣获“中国最具影响力企业”、“中国最具成长性企业”、“全球百佳儒商企业”、“品牌中国华谱奖”、“央视财经50十佳回报公司”等多项殊荣；获得了省级标准化良好行为4A级企业认证；取得了HACCP体系认证证书；荣获国家“安全生产标准化一级企业”称号，成为白酒行业首家达标企业。汾酒品牌荣登影响世界中国力量品牌500强，汾酒、竹叶青酒通过国家酒类等级（优级）标志认证，获评“网友最信赖的食品品牌”。")]),_c('p',[_vm._v("在变革、创新、超越、领先的道路上，公司将一如既往地贯彻“品质诚实、服务诚心、产业诚信”精神，为消费者提供优质产品，为股东多创红利，为职工多谋福利，实现“中国酒魂 清香天下”的汾酒梦！")])])}]

